<template>
    <div class="listing mt-2">
        <loader :is-visible="isLoading"></loader>

        <div class="table-responsive">
            <table class="table table-bordered mt-0" id="itmgrplisting">
                <thead class="thead-bg">
                    <tr>
                        <th class="text-center"><span href="javascript:;" id="TAG">#</span></th>
                        <th class="text-center"><span href="javascript:;" id="TAG">{{ $t('ProductName') }}</span></th>
                        <th class="text-center"><span href="javascript:;">{{ $t('TermsAndConditions') }}</span></th>
                        <th class="text-center"><span href="javascript:;">{{ $t('AvailableQuantity') }}</span></th>
                        <th class="text-center"><span id="group_name">{{ $t('PendingCheckOutRequest') }}</span></th>
                        <th class="text-center"><span id="ReadTime">{{ $t('RequestQuantity') }} </span></th>
                        <th class="text-center"><span id="AttachedItems">{{ $t('Compartment') }}</span></th>
                        <th class="text-center"><span id="AttachedItems">{{ $t('Action') }} </span></th>
                    </tr>
                </thead>
                <tbody v-if="listData.length > 0">
                    <template v-for="(item, index) of listData">

                        <tr :key="index"
                            v-bind:class="{ 'showassetcataloguegroup': true, 'insertedrow bg-gold': (item.AssetCatalogueGroup.length > 0), 'low-bar': (item.IsContainer == 1) }">
                            <td class="textaligncenter align-middle item-data text-center">
                                <span v-if="item.AssetCatalogueGroup.length > 0" v-on:click="ShowChild(item)"
                                    v-bind:class="{ 'togglearrow arrowwhitecollapse_roles  fa-2x showcatalog showassetcatalog': true, 'fa fa-caret-right': item.IsArrowRight == true, 'fa fa-caret-down': !item.IsArrowRight == true }"></span>

                            </td>

                            <td class="align-middle ad item-name text-center">{{ `${item.AssetCatalogName}
                                (${item.AssetUniqueName})` }} <span v-if="item.Mandatory"
                                    class="text-danger font-weight-bold mandatory">*</span> </td>
                            <td v-if="item.IsEnableTerms == 0" class="align-middle terms-condition text-center"
                                style="white-space:normal;">
                                <span>N/A</span>
                            </td>
                            <td class="align-middle terms-condition text-center" v-else>
                                <div class="custom-control custom-checkbox">
                                    <input class="chkItems custom-control-input" type="checkbox" name="checkbox"
                                        :checked="item.IsEnableTerms == 1">
                                    <label class="custom-control-label universal-custom-control-label pt-1"></label>
                                </div>

                            </td>

                            <td class="textalignright align-middle text-center">
                                {{ item.AvailableItems }}
                            </td>

                            <td class="textalignright align-middle text-center">
                                {{ item.PendingAvailableQuantity }}
                            </td>

                            <td class="align-middle">
                                <span v-if="item.AssetGroupId > 0">
                                    <span v-if="item.CreateUniqueNameListing == true">
                                        <input disabled type="text"
                                            :value="item.CreateUniqueNameListing == true ? 1 : item.PendingTotalAvailableItems"
                                            class="form-control txtitems textfield txtAssetQuentity  textalignright align-middle" />

                                    </span>
                                    <span v-else>
                                        <Field name="Requested Quantity" :rules="{ 'required': true }"
                                            v-slot="{ errors }">
                                            <input v-on:input="checkQuantity($event, item)" :min="1"
                                                v-model="item.PendingTotalAvailableItems" type="Number" :maxlength="8"
                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                        </Field>
                                    </span>
                                </span>

                                <span v-else>
                                    <span v-if="item.CreateUniqueNameListing == true">
                                        <input disabled type="text"
                                            :value="item.CreateUniqueNameListing == true ? 1 : item.PendingTotalAvailableItems"
                                            class="form-control txtitems textfield txtAssetQuentity  textalignright align-middle" />
                                    </span>
                                    <span v-else>
                                        <Field name="Requested Quantity" :rules="{ 'required': true }"
                                            v-slot="{ errors }">
                                            <input v-on:input="checkQuantity($event, item)"
                                                v-model="item.PendingTotalAvailableItems" type="Number" :maxlength="8"
                                                :min="1"
                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" />
                                        </Field>
                                    </span>
                                </span>

                            </td>

                            <td class="align-middle text-center">
                                <span>N/A</span>
                            </td>

                            <td class="text-center align-middle single-action action-group">

                                <a v-if="item.HasRelatedProducts" href="javascript:;" class="relatedproduct"
                                    v-on:click="relatedProductDetails(item, index)" title="Related items">
                                    <i class="fa fa-sitemap text-primary" aria-hidden="true"></i>
                                </a>

                                <a href="javascript:;" v-if="item.IsDelete" class="recyclebin  enable"
                                    v-on:click="deleteItem(item)" title="Delete"><i
                                        class="fa fa-trash text-danger action-icon"></i></a>

                            </td>
                        </tr>

                        <tr v-if="true" v-for="(citem, cindex) of item.AssetCatalogueGroup" :key="cindex"
                            v-bind:class="{ 'insertedrowchild': true, 'low-bar': (citem.IsContainer == 1) }">
                            <td class="align-middle item-data">
                                <span id="cataloguegroup_id" class='hidden showcatalog'>
                                </span>
                            </td>
                            <td class="align-middle item-name text-center">
                                {{ citem.AssetCatalogName }}
                            </td>
                            <td class="align-middle terms-condition text-center">
                                <span v-if="citem.IsEnableTerms == 1">
                                    <div class="form-group custom-control custom-checkbox">
                                        <input type="checkbox" v-model="citem.AssetCatalogueId"
                                            class="assertermscondition custom-control-input aa" name="example1" />
                                        <label class="custom-control-label universal-custom-control-label mt-1">
                                            {{ $t('AgreeToTermsConditions') }}
                                            <a style="text-decoration:none" href="javascript:;"
                                                :data-path="citem.AttachmentPath" :data-name="citem.AttachmentName"></a>
                                        </label>
                                    </div>
                                </span>
                                <span v-else>NA</span>
                            </td>
                            <td class="align-middle text-center">
                                {{ citem.AvailableItems }}
                            </td>
                            <td class="align-middle">
                                {{ citem.PendingAvailableQuantity }}
                            </td>
                            <td class="align-middle">

                                <input v-if="item.AssetGroupId > 0" data-numberonly="yes"
                                    class="form-control txtitems textfield txtAssetQuentity" type="decimal"
                                    v-model="citem.AsseetGroupQuantity" />

                                <input v-else data-numberonly="yes"
                                    v-bind:class="{ 'form-control txtitems textfield txtAssetQuentity': true, 'is-invalid': (citem.AsseetGroupQuantity == 0 || citem.AsseetGroupQuantity == '' || citem.AsseetGroupQuantity == null) }"
                                    type="decimal" v-model="citem.AsseetGroupQuantity" />

                                <span></span>
                            </td>
                            <td class="lastrow align-middle">
                                <select class="disabled_grey form-control text-secondary disabled" disabled="disabled">
                                    <option value="">Select</option>
                                    <option v-for="selct in item.ContainerList" selected="selected"
                                        :value="selct.Value">
                                        {{ selct.Text }}</option>
                                </select>
                            </td>
                            <td class="text-center align-middle action-group">
                                <a href="javascript:;" class="ancremoveData  recyclebin  enable"
                                    v-on:click="deleteChild(item)" title="Delete">
                                    <i class="fa fa-trash text-danger action-icon"></i>
                                </a>
                            </td>

                        </tr>
                    </template>
                </tbody>
                <tbody v-else>
                    <tr v-if="NoRecordfound">
                        <td colspan="8" class="text-center text-danger no-record">{{ $t('NoRecordfound') }}</td>
                    </tr>
                </tbody>
            </table>

            <RelatedProduct v-if="isVisible" :ItemIndex="ItemIndex" :GetRelatedProduct="GetRelatedProduct"
                @RelatedProduct="RelatedProduct">
            </RelatedProduct>

        </div>

        <div class="col-lg-12 text-right mt-3 float-left">
            <a href="javascript:;" id="btnSave" :disabled="isButtonDisabled" v-on:click.prevent="handleClick"
                class="btn btn-success " title="Save" name="btnSave" isclicked="No"><em class="fa fa-save pr-2"></em>{{
                    $t('Save') }} </a>
            <a @click="onCancel()" title="Cancel" class="btn btn-danger HideTab m-1"><em
                  id="backtolist"  class="fa fa-close pr-2"></em>{{
                        $t('Cancel') }}</a>
        </div>
    </div>
</template>

<script>
import RelatedProduct from "../Modal/RelatedProductPopup.vue";
import DataService from '../../service/DataService';
import { DateTime } from 'luxon';
export default {

    components: {
        RelatedProduct
    },
    props: {
        listData: {
            type: Array
        }
    },

    data() {

        return {
            NoRecordfound: true,
            isVisible: false,
            GetRelatedProduct: [],
            isArrowRight: true,
            Timezone: '',
            isButtonDisabled: false,
            isLoading: false
        }
    },
    created: function () {
        var vm = this;
        vm.Timezone = this.GetUserInfo.TimeZone;
    },
    methods: {

        RelatedProduct(element) {

            var vm = this;

            // vm.listData.push(...element);
            vm.CloseRelatedPopup();


        },
        ShowChild: function (item) {
            var vm = this;
            //vm.AssetCatalogueGroup[index].isArrowRight=!item.isArrowRight;
            item.IsArrowRight = !item.IsArrowRight;

        },
        deleteChild: function (item) {
            item.AssetCatalogueGroup = [];

        },
        onCancel: function () {
            const urlParams = new URLSearchParams(window.location.search);
            const reqfrom = urlParams.get('From') ? urlParams.get('From') : urlParams.get('From');
            if (reqfrom == 'ManageProdcut') {
                this.$router.push({ name: "ProductListing" });
            }
            else {
                this.$router.push({ name: "MyProducts" });
            }
            // else if (reqfrom == 'MyProdcut') {
            //     this.$router.push({ name: "MyProducts" });
            // }
            // else {
            //     this.$router.push({ name: 'ProductTypeCheckOutRequest' });
            // }

        },
        handleClick() {
            if (!this.isButtonDisabled) {
                this.isButtonDisabled = true;
                this.saveCheckout();
            }
        },

        saveCheckout: function () {
            var vm = this;
            var dataArray = [];
            var obj = {};
            var mainObj = {};
            var msg = "";
            var msgs = "";
            var chkValue = "";
            var chkValueAvailable = "";
            var itemnames = "";
            var itemList = [];
            var result = true;
            vm.isLoading = true;
            if (vm.listData.length == 0) {
                vm.isLoading = false;
                return vm.ShowAlert(vm.$t("Please add at least one item"), "warning", vm.$t('Alert'));
            }

            if (result) {
                var vm = this;
                vm.listData.forEach(ele => {
                    if (ele.AssetCatalogueId != undefined) {
                        let obj = {}

                        if (ele.ParentUniqueId > 0) {
                            obj = {
                                AssetCatalogUniqueId: ele.ParentUniqueId,
                                AssetCatalogId: ele.AssetCatalogueId,
                                AssetQuantity: ele.PendingTotalAvailableItems,
                                LocationId: ele.LocationId
                            }
                        } else {
                            obj = {
                                AssetCatalogUniqueId: 0,
                                AssetCatalogId: ele.AssetCatalogueId,
                                AssetQuantity: ele.PendingTotalAvailableItems,
                                LocationId: ele.LocationId
                            }
                        }

                        var itemId = `${ele.AssetCatalogName}(${ele.ItemCode})`;
                        if (itemList.indexOf(itemId) == -1) {
                            itemList.push(itemId);
                        }
                        if (!dataArray.some(x => x.AssetCatalogUniqueId == obj.AssetCatalogUniqueId && x.AssetCatalogId == obj.AssetCatalogId)) {
                            dataArray.push(obj);
                        }

                        if (ele.AssetCatalogueGroup.length > 0) {
                            ele.AssetCatalogueGroup.forEach(p => {
                                p.AsseetGroupQuantity = p.AsseetGroupQuantity == null ? 0 : p.AsseetGroupQuantity;

                                if (parseInt(p.AsseetGroupQuantity) == 0) {
                                    vm.isLoading = false;
                                    result = false;
                                    return vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning", vm.$t('Alert'));
                                }

                                var objNew = {
                                    AssetCatalogUniqueId: 0,
                                    AssetCatalogId: p.AssetCatalogueId,
                                    AssetQuantity: p.AsseetGroupQuantity,
                                    LocationId: p.LocationId
                                }

                                itemId = `${p.AssetCatalogName}(${p.ItemCode})`;
                                if (itemList.indexOf(itemId) == -1) {
                                    itemList.push(itemId);
                                }

                                if (!dataArray.some(x => x.AssetCatalogUniqueId == objNew.AssetCatalogUniqueId && x.AssetCatalogId == objNew.AssetCatalogId)) {
                                    dataArray.push(objNew);
                                }
                            });

                        }

                        itemnames = itemList.join(',');
                    }
                });

                if (!result) {

                    return false;

                } else {
                    var Data = {
                        UserId: vm.$parent.UserId,
                        // AssignedFrom: vm.formatTimeToCustomFormat(vm.$parent.DateofAssignment),
                        // AssignedTill: vm.formatTimeToCustomFormat(vm.$parent.DateofAssignmentTill),                        
                        AssignedFrom: (vm.$parent.DateofAssignment),
                        AssignedTill: (vm.$parent.DateofAssignmentTill),
                        Comment: vm.$parent.Remarks,
                        GroupRequistionAssestInfo: dataArray,
                        itemNames: itemnames
                    }
                    var mainObj = JSON.stringify(Data);

                    DataService.GetCheckOut().then((respnse) => {
                        if (respnse.data.maxRequisition_Time != 0) {
                            DataService.SaveGroupRequisition(mainObj).then(response => {
                                if (response.data.msg == 'success') {
                                    vm.isLoading = false;
                                    vm.isButtonDisabled = false;
                                    vm.ShowAlert(vm.$t("Check out request Submitted successfully"), "success", vm.$t('Alert'));
                                    vm.$router.push({
                                        path: "/AssetRequisition/MyRequisitions"
                                    });
                                } else {
                                    vm.isLoading = false;
                                    vm.isButtonDisabled = false;
                                    vm.ShowAlert(vm.$t("Something went wrong "), "warning", vm.$t('Alert'));
                                }
                            })

                        }
                        else {
                            vm.ShowAlert(vm.$t('MaxRequisitionCountMessage'), "failure", true, vm.$t('Alert'));
                            vm.isLoading = false;
                        }
                    });

                }
            } else {
                vm.isLoading = false;
                vm.isButtonDisabled = false;
                vm.ShowAlert(vm.$t("Please Correct the Highlighted errors"), "warning", vm.$t('Alert'));
            }

        },

        checkQuantity: function (event, item) {
            debugger;
            var vm = this;
            // Check if the pressed key is a minus sign and prevent input
            if (event.data === '-' || event.data === 'e') {
                item.PendingTotalAvailableItems = ''; // Clear the value
                return vm.ShowAlert(vm.$t('Please enter a valid quantity.'), "warning", vm.$t("Alert"));
                // Assuming showAlert is a method to show alerts
            }
            if (event.target.value == '0') {
                item.PendingTotalAvailableItems = "";
                return vm.ShowAlert(
                    vm.$t("Quantity must be at least 1. "),
                    "warning",
                    vm.$t("Alert")
                );

            }

            let TotalQuantity = item.AvailableItems;
            let currentValue = item.PendingTotalAvailableItems;

            if (currentValue > TotalQuantity) {
                vm.listData.forEach(ele => {
                    if (ele.AvailableItems == ele.PendingAvailableQuantity) {
                        ele.PendingTotalAvailableItems = "";
                        vm.ShowAlert(
                            vm.$t("Requested quantity not available. "),
                            "warning",
                            vm.$t("Alert")
                        );
                        return;
                    }
                    if (ele.AssetCatalogueId == item.AssetCatalogueId) {
                        vm.ShowAlert(vm.$t('RequestQuanitityNotMoreThen'), "warning", true, vm.$t("Alert"));
                        ele.PendingTotalAvailableItems = "";
                        return;
                    }
                    if (currentValue === null || currentValue === undefined) {
                        vm.ShowAlert(vm.$t('Please enter a valid quantity.'), "warning", vm.$t("Alert"));
                    }
                });
            } else {
                return;
            }
        },



        relatedProductDetails(item, indx) {

            var vm = this;
            vm.GetRelatedProduct = [];
            let parsedObject = JSON.parse(item.objProduct);
            let products = parsedObject.RESULT;
            let parsedProducts = JSON.parse(products);
            parsedProducts.forEach(function (product) {

                product.Quantity = 1;
                product.itemDetail = item;

                if (product.Mandatory == true) {
                    ///
                } else {
                    //
                    vm.GetRelatedProduct.push(product);
                }

            });

            if (vm.GetRelatedProduct.length > 0) {
                vm.isVisible = true;

            }


        },


        deleteItem(item) {

            var vm = this;
            vm.confirmR(vm.$t('Please verify that you want to delete this record'), true, false, function (result) {

                const index = vm.listData.findIndex(x => x.AssetCatalogueId === item.AssetCatalogueId);

                if (index !== -1) {
                    vm.listData.splice(index, 1);
                }

                return vm.ShowAlert(
                    vm.$t("Item has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                );

            });
        },

        CloseRelatedPopup() {
            //debugger
            var vm = this;
            vm.isVisible = false;
        },
        UpdateProductList(qty, ind) {

            var vm = this;
            vm.listData[ind].PendingTotalAvailableItems = qty;
            vm.isVisible = false;
        },

        formatTimeToCustomFormat(inputTime) {
            if (inputTime != null && inputTime !== '') {
                const inputLuxonDateTime = DateTime.fromJSDate(new Date(inputTime), { zone: 'utc' }); // Assume the input time is in UTC               
                const targetTimeZone = `Etc/GMT${this.Timezone}`;
                const outputLuxonDateTime = inputLuxonDateTime.setZone(targetTimeZone);
                const formattedDateTime = outputLuxonDateTime.toFormat("MM/dd/yyyy h:mm");
                return formattedDateTime;
            } else {
                return "";
            }
        }

    }

}
</script>

<style>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}
</style>
