                                                            :value="LocationValueForBarCode">
                                                            {{ LocationName }}
                                                        </option>
                                                    </select>
                                                    <span v-if="errors[0]" name="Location"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>

                                            </div>

                                            <div
                                                class="col-12 col-sm-3 col-md-3 col-lg-3 float-left pl-md-0 pr-md-0 mt-0 mt-md-4 mt-sm-4">
