                                        <div class="row">
                                            <div class="col-md-6 pl-0">
                                                <div class="form-group">
                                                    <label>{{ $t('ProductType') }}<span
                                                            class="text-danger">*</span></label>
                                                    <Field name="Product Type" class="w-100" as="select"
                                                        :rules="{ 'required': true }" v-slot="{ errors }">
                                                        <select name="Product Type" v-model="txtProductType"
                                                            v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                                            @change="getProductNameList(txtProductType)">
                                                            <option value="">{{ $t('Select') }}
                                                            </option>
                                                            <option v-for="(item, itemIndex) in productTypeList"
                                                                :key="itemIndex" :value="item.ASSETID">{{ item.ASSETTYPE
                                                                }}
                                                            </option>
                                                        </select>

                                                        <span v-if="errors[0]" name="Product Type"
                                                            class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                            <div class="col-md-6">
                                                <div class="form-group">
                                                    <div class="form-group">
                                                        <label>{{ $t('ProductName') }}<span
                                                                class="text-danger">*</span></label>

                                                        <Field name="Product Name" class="w-100" as="select"
                                                            :rules="{ 'required': true }" v-slot="{ errors }">
                                                            <select name="Product Name" v-model="txtProductName"
                                                                v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                                                @change="getProductItemNameList(txtProductName)">
                                                                <option value="">{{ $t('Select') }}
                                                                </option>
                                                                <option v-for="(item, itemIndex) in productNameList"
                                                                    :key="itemIndex" :value="item.Value">{{ item.Text }}
                                                                </option>
                                                            </select>
                                                            <span v-if="errors[0]" name="Product Name"
                                                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                        </Field>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div class="row">
                                            <div class="col-md-6 pl-0">

                                                <label> {{ $t('SelectItem') }}</label>
                                                <div class="d-flex" style="align-items: flex-start;">
                                                    <Field name="Products" :rules="{ 'required': false }"
                                                        label="ItemName" v-slot="{ errors }" style="width:85%">
                                                        <div class="input-group mb-3" style="position: relative;">
                                                            <input name="Products" id="Products" type="text"
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                v-model="productUniqueName" value="iselectVal" />

                                                            <a @click="itemListOnClick" class="btn float-left"
                                                                href="javascript:;"
                                                                style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"><em
                                                                    class="dropdown-toggle float-right"></em></a>
                                                            <span v-if="errors[0]" name="Products"
                                                                class="invalid-feedback d-block">{{
                                                                    errors[0] }}</span>

                                                            <div class="unique_dynamicdatalist">
                                                                <ul v-for="(item, index) in productItemNameList"
