                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>


                                    </div>
                                </Form>
                                <div class="row">
                                    <div class="col-12 mb-2 mt-2">
                                        <div class="mr-2 d-inline-block">
                                            <span class="legend-Progress colorboxsquare"></span>{{
                                                $t('ContainerProducts')
                                            }}
                                        </div>
                                    </div>
                                </div>
                                <Form ref="Quantity" class="listing">
                                    <div class="table-responsive table-fix-header">
                                        <table id="tableuniquename"
                                            class="table table-bordered dt-responsive nowrap my-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th><span style="max-width: 7px;">#</span></th>
                                                    <th><span>{{ $t('ProductName') }}</span></th>
                                                    <th><span>{{ $t('AvailableQuantity') }}</span></th>
                                                    <th><span>{{ $t('PendingCheckOutRequests') }}</span></th>
                                                    <th><span>{{ $t('RequestQuantity') }}</span></th>
                                                    <th><span>{{ $t('Compartment')
                                                            }}</span></th>
                                                </tr>
                                            </thead>



                                            <tbody v-if="NoRecordfound == false">
                                                <tr :class="{ 'showassetcataloguegroup': true, 'low-bar': item.IsContainer }"
                                                    v-for="(item, index) in searchProductData" :key="index">

                                                    <td v-if="item.AvailableItems == 0"
                                                        class="textaligncenter align-middle item-data">
                                                        <div class="custom-control custom-checkbox">
                                                            <!-- <input class="chkItems custom-control-input" v-model="item.IsChecked" :checked="true" type="checkbox"
                                                                disabled name="checkbox">
                                                            <label
                                                                class="custom-control-label universal-custom-control-label pt-1"></label> -->

                                                            <input disabled class="" type="checkbox" name="checkbox" />
                                                        </div>

                                                    </td>

                                                    <td v-else class="textaligncenter align-middle item-data">
                                                        <div class="custom-control custom-checkbox">
                                                            <input v-model="item.IsChecked" class="chkItems"
                                                                type="checkbox" name="checkbox" />
                                                        </div>

                                                    </td>



                                                    <td class="align-middle ad item-name">{{ item.Name }}</td>

                                                    <td class="textalignright align-middle">
                                                        {{ item.AvailableItems
                                                        }}
                                                    </td>
                                                    <td class="textalignright align-middle">
                                                        {{
                                                            item.PendingAvailableQuantity }}
                                                    </td>
                                                    <td class="align-middle" v-if="item.AvailableItems > 0">
                                                        <!-- 
                                                        <input type="number" min="1"
                                                            v-model="item.PendingTotalAvailableItems"
                                                            class="form-control txtitems textfield txtAssetQuentity textalignright align-middle ">
                                                        <span></span> -->

                                                        <Field name="Requested Quantity" :rules="{ 'required': true }"
                                                            v-slot="{ errors }">
                                                            <input @keyup="checkQuantity(item)"
                                                                v-model="item.PendingTotalAvailableItems" type="number"
                                                                min="1" maxlength="8"
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0], 'disabled': item.AvailableItems == 1 }" />

                                                        </Field>


                                                    </td>
                                                    <td class="align-middle" v-else>
                                                        <input disabled type="text"
                                                            v-model="item.PendingTotalAvailableItems"
                                                            v-bind:class="{ 'form-control txtitems textfield txtAssetQuentity textalignright align-middle': true }">
                                                        <span></span>
                                                    </td>

                                                    <td class="align-middle">
                                                        <span>N/A</span>
                                                    </td>

                                                </tr>


                                            </tbody>

                                            <tbody v-else>

                                                <tr>
                                                    <td colspan="8" class="text-center text-danger no-record">{{
                                                        $t('NoRecordfound') }}</td>
                                                </tr>
                                            </tbody>

                                        </table>
                                    </div>
                                    <div v-if="isButtonVisible == true" class="col-lg-12 text-right mt-3 float-left">
                                        <a href="javascript:;" id="btnSave" class="btn btn-success " title="Save"
                                            v-on:click="addToList"><em class="fa fa-save pr-2"></em>{{
                                                $t('AddToList') }} </a>
                                        <a v-on:click="$emit('CloseSearchItem')" class="btn btn-danger m-1"><em
                                                class="fa fa-close pr-2"></em>{{
                                                    $t('Close') }}</a>
                                    </div>
                                </Form>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </section>
</template>
