            UserId: null,
            options: [],
            txtProductName: '',
            txtProductType: '',
            productItemNameList: [],
            showProductDropdown: false,
            productUniqueName: '',
            productUniqueId: null,
            searchProductData: [],
            addListData: [],
            selectedValues: '',
            isSelected: false,
            isButtonVisible: false,
            LocationValueForBarCode: '',
            isLoading: false,
            maxRequisitionTime: 0,
            isTimeron: 0,
            requsitionTime: 0,
            UserName: '',
        }
    },
