        var vm = this;
        console.log("h1", vm.TreeValue);
        var UserInfo = vm.GetUserInfo;
        vm.UserId = UserInfo.ID;
        vm.UserName = UserInfo.Name;
        vm.ChangeSearchValue(vm.SearchValue);
        vm.getProductTypeList();
        // vm.GetLocationCheckOutByuserId();
    },

    methods: {
