        //                     locationId = value.COMPANY_LOCATION_ID
        //                     let rslt = vm.getChildLocation(locationId, value, response.data.data)
        //                     vm.FindalLocationArray.push(rslt)
        //                 }
        //             }
        //         });
        //         vm.options = vm.FindalLocationArray;
        //     });
        // },

        // getChildLocation: function (locationId, currentItem, data) {
        //     let ths = this;
        //     let treeArray = {};
        //     treeArray = {
        //         id: currentItem.COMPANY_LOCATION_ID,
        //         label: currentItem.LOCATION_NAME
        //     }
        //     let retrunArray = treeArray;
        //     var childArray = data.filter(function (el) {
        //         return el.PARENT_LOCATION_ID == locationId;
        //     });
        //     let finalChildArray = [];
        //     childArray.forEach((value) => {
        //         let ChildfolderId = value.COMPANY_LOCATION_ID
        //         var CheckIfExist1 = finalChildArray.filter(function (el) {
        //             return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
        //         });
        //         if (CheckIfExist1.length == 0) {
        //             var CheckIfExist = data.filter(function (el) {
        //                 return el.PARENT_LOCATION_ID == ChildfolderId;
        //             });
        //             if (CheckIfExist != null && CheckIfExist.length > 0) {
        //                 let rslt = ths.getChildLocation(ChildfolderId, value, data);
        //                 finalChildArray.push(rslt);
        //             } else {
        //                 treeArray = {
        //                     id: `${value.COMPANY_LOCATION_ID}`,
        //                     label: `${value.LOCATION_NAME}`
        //                 }
        //                 finalChildArray.push(treeArray);
        //             }
        //         }
        //     });
        //     retrunArray.children = finalChildArray;
        //     return retrunArray;
        // },

        searchByProduct() {

            debugger;
            var vm = this;
            vm.isLoading = true;

            vm.$refs.product.validate().then(result => {
                if (result) {

                    let currentDate = new Date();
                    let today = new Date()
                    let currentTime = new Date();
                    today.setDate(currentDate.getDate() + 1);
                    currentDate = vm.$options.filters.formatDateYear(currentDate);
                    var endDate = vm.$options.filters.formatDateYear(today);
                    var startTime = vm.$options.filters.formatTimee(currentTime);
                    var endTime = vm.$options.filters.formatTimee('12:00 AM');
                    var url = `id=${vm.txtProductType}&isView=1&barcode=&assetType=${vm.txtProductType}&assetUniqueId=${vm.productUniqueId != null ? vm.productUniqueId : ''}&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=&issueTo=${vm.UserId}&assignedFrom=${currentDate}&assignedTill=${endDate}&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&startTime=${startTime}&endTime=${endTime}&uniqueIds=${vm.productUniqueId != null ? vm.productUniqueId : ''}&catalogueIds=${vm.txtProductName}`;

                    DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {

                        if (response.data.length > 0) {

                            vm.searchProductData = response.data;

                            vm.searchProductData.forEach(ele => {
                                ele.IsChecked = false;
                                ele.ReqQty = '';

                            })
                            vm.NoRecordfound = false;
                            vm.isButtonVisible = true;

                            setTimeout(function () {
                                vm.CheckBoxBootstrap();
                            }, 100);

                            vm.isLoading = false;
                        }
                        else {
                            vm.NoRecordfound = true;
                            vm.searchProductData = []
                            vm.isLoading = false;
                        }
                    });
                }
                else {
                    vm.isLoading = false;
                }

            })

        },

        searchByBarcode: function () {

            var vm = this;
            vm.isLoading = true;
            vm.$refs.barcode.validate().then(result => {

                if (result) {
                    var vm = this;
                    vm.isuniqueSearch = 'yes';
                    var url = `id=&isView=1&barcode=${this.txtBarcode}&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=search&isuniqueSearch=${vm.isuniqueSearch}&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.LocationValueForBarCode}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
                    DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {

                        if (response.data.length > 0) {
                            vm.isLoading = false;
                            vm.searchProductData = response.data;
                            vm.NoRecordfound = false;
                            vm.searchProductData.forEach(ele => {
                                ele.IsChecked = false;
                                ele.ReqQty = '';

                            })
                            vm.isButtonVisible = true;
                            // setTimeout(function () {
                            //     vm.CheckBoxBootstrap();
                            // }, 100);
                        }
                        else {
                            vm.isLoading = false;
                            vm.NoRecordfound = true;
                            vm.isButtonVisible = true;
                        }
                    })
                }

                this.isLoading = false;

            })
        },
        clear: function () {
            var vm = this;
            vm.txtBarcode = '';
            vm.LocationValueForBarCode = '';
        },
        searchfilter: function () {
            var vm = this;
            vm.LocationValueForBarCode = '';
            var url = `id=&isView=1&barcode=${vm.txtBarcode}&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=search&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {

                if (response.data[0] != null) {
                    // var Locations = vm.options.filter(x => x.id == response.data[0].LocationId);
                    if (response.data.length > 0) {
                        vm.isLoading = false;
                        vm.LocationName = response.data[0].LocationName;
                        vm.LocationValueForBarCode = response.data[0].LocationId;
                        vm.isButtonVisible = true;
                    } else {
                        vm.isLoading = false;
                        vm.ShowAlert(vm.$t(`${response.data[0].LocationName} Location's Product not Assigning To ${vm.UserName}`), "warning", true, vm.$t('Alert'));
                    }

                } else {
                    vm.ShowAlert(vm.$t('NoRecordfound'), "warning", true, vm.$t('Alert'));
                    vm.isLoading = false;
                }

            })

        },


        addToList: async function () {

            debugger;


            var vm = this;
            vm.isLoading = true;
            var assestUniqueId = "";
            let currentDate = new Date();
            let today = new Date()
            let currentTime = new Date();
            currentDate = vm.$options.filters.formatDateYear(currentDate);
            var endDate = vm.$options.filters.formatDateYear(today);
            var startTime = vm.$options.filters.formatTimee(currentTime);
            var endTime = vm.$options.filters.formatTimee('12:00 AM');
            var items = vm.searchProductData.filter(x => x.IsChecked);
            var assestUniqueId = "";
            var assetType = vm.txtProductType;
            var assetCatalog = vm.txtProductName;
            vm.addListData = items;


            await DataService.GetCheckOut().then((respnse) => {
                if (respnse.data.maxRequisition_Time != 0) {
                    var itemIds = items.map(x => x.ParentUniqueId).join(',');
                    if (items.length == 0) {
                        vm.isLoading = false;
                        vm.ShowAlert(
                            vm.$t("Please select one/multiple checkbox(s) to add the record(s)."),
                            "warning",
                            vm.$t("Alert")
                        );

                    }
                    var url = `id=${items[0].AssetCatalogueId}&isView=1&barcode=&assetType=${items[0].AssetTypeId}&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=no&issueTo=${vm.UserId}&assignedFrom=${currentDate}&assignedTill=${endDate}&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&startTime=${startTime}&endTime=${endTime}&uniqueIds=${itemIds}&catalogueIds=`;

                    if (vm.SearchValue == 'Barcode') {
                        url = `id=&isView=1&barcode=${vm.txtBarcode}&assetType=&assetUniqueId=${vm.searchProductData[0].ParentUniqueId}&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=no&issueTo=${vm.UserId}&assignedFrom=${currentDate}&assignedTill=${endDate}&locationId=${vm.searchProductData[0].LocationId}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&startTime=${startTime}&endTime=${endTime}&uniqueIds=${vm.searchProductData[0].ParentUniqueId}&catalogueIds=`;

                    }


                    items.forEach(function callback(ele, index) {
                        if (ele.PendingTotalAvailableItems == '' || ele.PendingTotalAvailableItems == 0) {
                            vm.ShowAlert(
                                vm.$t("Quantity should more then 1"),
                                "warning",
                                vm.$t("Alert")
                            );
                            vm.isLoading = false;
                        }
                        else {
                            DataService.GetCompanyAssestCatalogsAddToList(url).then(response => {
                                debugger;

                                if (response.data.length > 0) {
                                    vm.addListData = response.data;
                                    vm.addListData.forEach(function callback(ele, index) {
                                        if (ele.IsContainer == 1 && ele.AssetCatalogueGroup.length > 0) {
                                            ele.IsArrowRight = true;
                                            // ele.AssetCatalogueGroup.forEach(function callback(ele1, index1){
                                            //     ele1.isArrowRight=false;

                                            // })
                                        }
                                        items.forEach(function callback(itemSearch, index) {

                                            if (itemSearch.AssetCatalogueId == ele.AssetCatalogueId) {
                                                ele.PendingTotalAvailableItems = itemSearch.PendingTotalAvailableItems;

                                            }

                                        })
                                    })



                                    // if (vm.$parent.listData.length == 0) {
                                    //     debugger;
                                    //     vm.$parent.listData.forEach(ele => {
                                    //         var i = vm.addListData.map(item => item.AssetCatalogueId).indexOf(ele.AssetCatalogueId);
                                    //         if (i != -1) {
                                    //             vm.addListData.splice(i, 1);
                                    //         }
                                    //     });

                                    // }


                                    vm.updateListData();

                                    vm.maxRequisitionTime = respnse.data.maxRequisitionTime;

                                    vm.requsitionTime = parseInt(vm.maxRequisitionTime) / 60;
                                    vm.isTimeron = 0;
                                    if (parseInt(vm.maxRequisitionTime) <= 0) {
                                        vm.isTimeron = 1;
                                    }

                                    vm.$parent.GetCheckOutDetails();
                                    //vm.$parent.GetCheckOutDetails();
                                    vm.isLoading = false;
                                }


                            });
                        }
                    });
                }
                else {
                    vm.ShowAlert(vm.$t('MaxRequisitionCountMessage'), "failure", true, vm.$t('Alert'));
                    vm.isLoading = false;
                }


            })
        },
        updateListData: function () {
            debugger;
            var vm = this;

            // vm.addListData.forEach(ele => {

            //     vm.$parent.listData.push(ele);
            // });
            vm.addListData.forEach(ele => {

                vm.$emit('addListData', ele);

            })


            vm.$parent.CloseSearchItem();
        },
        checkQuantity: function (item) {

            var vm = this;
            let TotalQuantity = item.AvailableItems;
            let currentValue = item.PendingTotalAvailableItems;
            if (currentValue > TotalQuantity) {


                vm.searchProductData.forEach(ele => {

                    if (ele.AvailableItems == ele.PendingAvailableQuantity) {
                        ele.PendingTotalAvailableItems = '';
                        return vm.ShowAlert(vm.$t("Requested quantity not available. "), "warning", vm.$t("Alert"));
                    }


                    if (ele.AssetCatalogueId == item.AssetCatalogueId) {
                        ele.PendingTotalAvailableItems = '';
                        return vm.ShowAlert(vm.$t('RequestQuanitityNotMoreThen', [TotalQuantity]), "warning", true, vm.$t("Alert"));

                    }
                })


            }
            else {
                return;
            }
        },

        ChangeSearchValue: function (ChangeValue) {

            if (ChangeValue == 'searchproduct') {
                this.showFirst = false;
            } else {
                this.showFirst = true;
            }
        },

        setProductItemName: function (item) {
            var vm = this;
            vm.productUniqueId = item.Value;
            vm.productUniqueName = item.Name;
            vm.showProductDropdown = false;

        },

        itemListOnClick: function () {
            if (this.showProductDropdown == false) {
                this.showProductDropdown = true
            }
            else {
                this.showProductDropdown = false
            }
        },

        getProductTypeList: function () {
            var vm = this;
            vm.isLoading = true;
            vm.productNameList = []
            vm.productUniqueName = '';
            var url = `type=&issueTo=${this.UserId}&locationId=${this.TreeValue}&requestType=&requestFrom=assetrequisition`;
            DataService.AssignGetAssetType(url).then(response => {
                vm.isLoading = false;
                vm.productTypeList = response.data;

            });

        },

        getProductNameList(event) {

            var vm = this;
            if (event == "") {
                vm.productNameList = [];
                return;
            }
            vm.productItemNameList = []
            vm.productUniqueName = '';
            vm.productUniqueId = '';
            vm.showProductDropdown = false
            vm.isLoading = true;
            var url = `id=${vm.txtProductType}&isView=&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=&issueTo=${vm.UserId}&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            console.log("url", url);
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {

                if (response.data.Result.length > 0) {
                    vm.productNameList = response.data.Result;
                    vm.isLoading = false;
                }
                else {
                    vm.productNameList = []
                    vm.isLoading = false;
                }

            });

        },

        getProductItemNameList: function () {
            var vm = this;
            vm.isLoading = true;
            vm.productUniqueName = '';
            vm.productUniqueId = '';
            vm.showProductDropdown = false
            var url = `assetCatalogId=${this.txtProductName}&name=&issueTo=${this.UserId}&locationId=${this.TreeValue}&requestFrom=assetrequisition&assetType=`;
            DataService.GetUniqueAssetItems(url).then(response => {
                if (response.data.length > 0) {
                    vm.isLoading = false;
                    vm.productItemNameList = response.data;
                }
                else {
                    vm.isLoading = false;
                    vm.productItemNameList = []
                }

            });

        },

    }
}
</script>

