<template>
    <section class="main-content">
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('RelatedProduct') }}
                            </h5>
                            <button v-on:click="ClosePopup()" type="button" class="close"
                                data-original-title="Close"><em class="fa fa-times"></em></button>
                        </div>


                        <div class="row">
                            <div class="col-lg-12">
                                <div class="listing">
                                    <div class="table-responsive table-fix-header">
                                        <table id="tblRelatedDataItems"
                                            class="table table-bordered dt-responsive nowrap my-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th><span style="max-width: 7px;">#</span></th>
                                                    <th><span>Product Name</span></th>
                                                    <th style="min-width: 300px;"><span>Available Quantity</span></th>
                                                    <th><span>Required Quantity</span></th>
                                                </tr>
                                            </thead>
                                            <tbody>

                                                <tr v-for="item in GetRelatedProductData" v-bind:key="item">


                                                    <td class="text-center">
                                                        <div class="custom-control custom-checkbox">
                                                            <input class="custom-control-input" type="checkbox"
                                                                name="checkbox" v-model="item.isSelected"
                                                                v-on:change="singleSelect($event, item)">

                                                            <label
                                                                class="custom-control-label universal-custom-control-label pt-1"></label>
                                                        </div>
                                                    </td>

                                                    <td>{{ item.NAME }}</td>
                                                    <td>{{ item.AVAILIABLE_ITEM }}</td>
                                                    <td>

                                                        <Field name="Quantity" :rules="{ 'required': true }"
                                                            v-slot="{ errors }">
                                                            <input inputmode="numeric"
                                                                v-on:keyup="checkQuantity(item, item.Quantity, index)"
                                                                id="" v-model="item.Quantity" min="1" maxlength="8"
                                                                v-bind:class="{ 'txtitems form-control ': true, 'is-invalid': errors[0] || item.Quantity == 0 ? true : false }" />
                                                            <span v-if="errors[0]" name="Location"
                                                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                        </Field>


                                                    </td>
                                                </tr>

                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-danger" @click="ClosePopup">{{ $t('Close')
                                        }}</button>
                                    <button type="button" id="relatedprdct" class="btn btn-success"
                                        @click="SaveChanges">{{
                                            $t('SaveChanges') }}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
