            LegendArray: [],
            GetRelatedProductData: [],
            IsChecked: false,
            txtAssetQuentity: 1,
            RelatedProduct: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPage: 0,
            TotalRecord: 0,
            noRecord: true,
            PageNumber: 1,
            PageSize: 10,
            UserId: null
        }
    },
    created: function () {
       

        var vm = this;
        var UserInfo = vm.GetUserInfo;
        vm.UserId = UserInfo.ID;
        if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {

            vm.GetRelatedProductData = vm.GetRelatedProduct;
          
        } else {
            vm.FetchData();
        }


    },
    methods: {



        singleSelect(event, item) {


            var vm = this;

            if (event.target.checked == true) {

                vm.GetRelatedProductData.forEach(element => {
                    //
                    if (element.ID == item.ID) {
                        element.isSelected = true;

                    }



                })
            }
            else {

