                // }, 400);
                // setTimeout(function () {
                //     $('#chkAll_0').trigger('click');
                //     vm.isLoading = false;
                // }, 600);

            })
        },


        SaveChanges() {

            let vm = this;
            let relatedProductIds = '';
            let productArray = [];

            vm.GetRelatedProductData.forEach(element => {

                if (element.isSelected) {

                    if (relatedProductIds !== '') {
                        relatedProductIds += ',';
                    }
                    relatedProductIds += element.ID;
                    productArray.push(element);

                }
            });

            console.log("relatedProductIds", relatedProductIds);

            if (relatedProductIds == '') {

                return vm.ShowAlert(vm.$t("PleaseSelectAtleastOneProduct"), "warning", vm.$t("Alert"));
            }
            else {
                //
                let currentDate = new Date();
                let today = new Date()
                let currentTime = new Date();

                var assignedFrom = vm.$options.filters.formatDateYear(currentDate);
                var assignedTill = vm.$options.filters.formatDateYear(today);

                // var assignedFrom = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignment);
                // var assignedTill = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignmentTill);

                var startTime = vm.$options.filters.formatTimee(currentTime);
                var endTime = vm.$options.filters.formatTimee('12:00 AM');

                productArray.forEach(ele => {


                    let catalogueId = relatedProductIds;
                    let assetType = ele.itemDetail.AssetTypeId;
                    let locationId = ele.itemDetail.LocationId;
                    let IssueTo = vm.UserId;
                    var uniqueIds = null;

                    var url = `id=${catalogueId}&isView=1&barcode=&assetType=${assetType}&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=no&issueTo=${IssueTo}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${locationId}&RFID=0&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${uniqueIds}&catalogueIds=${catalogueId}`;

                    DataService.GetCompanyAssestCatalogsDetalis(url).then((response) => {
                        vm.RelatedProduct = [];
                        vm.RelatedProduct = response.data;
                        vm.$emit('RelatedProduct', vm.RelatedProduct);

                        vm.ClosePopup();
                    });

                    // let catalogueId = relationType == 'ProductName' ? id : null;
                    // var uniqueIds = relationType == 'Product' ? id : null;

                    // var IssueTo = vm.$parent.$parent.$parent.$parent.Users == undefined ? this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.Users) : 0;
                    // var startTime = this.$options.filters.formatTime(assignedFrom);
                    // var endTime = this.$options.filters.formatTime(assignedTill);


                })





             


            }




        },






        // SaveChanges: function () {

        //     debugger;

        //     var vm = this;
        //     if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {
        //         vm.$parent.UpdateProductList(vm.txtAssetQuentity, vm.ItemIndex);
        //     } else {
        //         var locationId = vm.RelatedProductDaata[0].LocationId;
        //         var selectedProductArray = [];
        //         vm.AssetListData.forEach((item) => {
        //             selectedProductArray.push({
        //                 catalogueId: item.AssetCatalogueId,
        //                 uniqueId: item.ParentUniqueId
        //             })
        //         });
        //         vm.GetRelatedProductData.forEach((item, index) => {

        //             if (vm.IsChecked) {
        //                 var relationType = item.ID == '0' ? 'Product' : 'ProductName';
        //                 var id = relationType == 'ProductName' ? item.ID : vm.RelatedProductDaata[0].ParentUniqueId;
        //                 var isMandatory = item.Mandatory;
        //                 var relatedCatalogueId = vm.RelatedProductDaata[0].AssetCatalogueId;
        //                 var relatedUniqueId = vm.RelatedProductDaata[0].ParentUniqueId;
        //                 var AssetQuentity = vm.txtAssetQuentity;
        //                 if (selectedProductArray.some(x => x.catalogueId == vm.RelatedProductDaata[0].AssetCatalogueId && vm.RelatedProductDaata[0].ParentUniqueId)) {
        //                     var catalogueIds = relationType == 'ProductName' ? id : null;
        //                     var uniqueIds = relationType == 'Product' ? id : null;
        //                     var assignedFrom = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignment);
        //                     var assignedTill = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignmentTill);
        //                     var IssueTo = vm.$parent.$parent.$parent.$parent.Users == undefined ? this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.Users) : 0;
        //                     var startTime = this.$options.filters.formatTime(assignedFrom);
        //                     var endTime = this.$options.filters.formatTime(assignedTill);

        //                     var url = `id=&isView=1&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetassignment&isFor=&isuniqueSearch=&issueTo=${IssueTo}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${locationId}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${uniqueIds}&catalogueIds=${catalogueIds}`;

        //                     DataService.GetCompanyAssestCatalogsDetalis(url).then((response) => {
        //                         vm.RelatedProduct = response.data;
        //                         vm.$emit('RelatedProduct', vm.RelatedProduct);
        //                         vm.ClosePopup();
        //                     });
        //                 }
        //             } else {
        //                 vm.$parent.Isrelatedproduct = false;
        //             }
        //         });
        //     }
        // },

        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNum = value.PageNumber;
            this.FetchData();
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            if (this.activeTab == 1 || this.activeTab == 4) {
                this.FetchData();
            } else if (this.activeTab == 2) {
                this.GetAssetReplacement();
            } else {
                this.GetAssetReturnRequestList();
            }
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        ClosePopup: function () {
            // debugger
            this.$parent.CloseRelatedPopup();
        },
        handleClick() {
            if (this.$refs.theCheckbox[0].checked) {
                this.IsChecked = true;
            } else {
                this.IsChecked = false;
            }
        },
        commonHeaderButtonClick: function () { },
        renderActions() { },
        renderRowActionMethod() { },
    }
}
</script>

<style scoped>
input.custom-control-input {
    z-index: 1;
}

span.btn.d-inline-block.w-80.text-center.text-truncate.btn-warning {
    padding: 0;
}

span.btn.d-inline-block.w-80.h-20.text-center.text-truncate.btn-success {
