<template>
    <section class="main-content">
        <loader :is-visible="isLoadinges"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('NewCheckout') }}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <Form ref="addCheckOut" id="formCheckOut">
                <div class="col-md-12 p-0">
                    <div class="row">
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="input-group">
                                <label> {{ $t('DateofAssignment') }}:</label><span class="text-danger">*</span>
                                <v-date-picker :popover="popover" v-bind:name="DateofAssignment"
                                    v-bind:class="{ 'timedate input-group': true, 'disabled': datepickerDisabled }"
                                    mode="dateTime" v-model="DateofAssignment" :disabled="datepickerDisabled">
                                    <template v-slot="{ inputValue, inputEvents }">
                                        <input placeholder="M/D/YYYY h:mm A"
                                            v-bind:class="{ 'form-control': true, 'disabled': datepickerDisabled ? true : false }"
                                            :value="inputValue" v-on="inputEvents" />
                                        <div class="input-group-append"><span class="input-group-text"><em
                                                    v-on="inputEvents" class="fa fa-calendar"></em></span></div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="input-group">
                                <label>{{ $t('DateofAssignmentTill') }}: </label><span class="text-danger">*</span>
                                <v-date-picker :popover="popover" mode="dateTime" :disabled="datepickerDisabled"
                                    v-bind:name="DateofAssignmentTill"
                                    v-bind:class="{ 'timedate input-group': true, 'disabled': datepickerDisabled }"
                                    v-model="DateofAssignmentTill" :min-date="DateofAssignment">

                                    <template v-slot="{ inputValue, inputEvents }">
                                        <input placeholder="M/D/YYYY h:mm A"
                                            v-bind:class="{ 'form-control': true, 'disabled': datepickerDisabled ? true : false }"
                                            :value="inputValue" v-on="inputEvents" />
                                        <div class="input-group-append"><span class="input-group-text"><em
                                                    v-on="inputEvents" class="fa fa-calendar"></em></span></div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                            <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                <tree-view v-model="TreeValue" :options="options"
                                    v-on:input="BindItemTypeByUserId(TreeValue)" placeholder="Select Location"
                                    :disabled="treeViewDisabled" :show-count="true"
                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                </tree-view>

                                <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                    errors[0] }}</span>

                            </Field>
                        </div>
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label for="Comment">{{ $t('Comment') }}<span class="text-danger"></span></label>

                                <!-- <Field class="w-100" name="Comment" :rules="{ 'required': true }" v-slot="{ errors}"> -->

                                <textarea cols="3" rows="3" v-model="Remarks"
                                    v-bind:class="{ 'form-control w-100': true }"></textarea>
                                <!-- <span v-if="errors[0]" name="Comment" class="invalid-feedback d-block">{{ errors[0]}}</span> -->

                                <!-- </Field> -->


                            </div>
                        </div>
                    </div>
                </div>



                <div class="col-md-12 p-0">
                    <div class="heading-border mt-2">
                        <h5 class="h5"><span>{{ $t('List Of Product') }}</span>
                            <small class="float-right">
                                <a href="javascript:;" id="addProduct" class="additems" title=""
                                    data-toggle-tooltip="tooltip" data-original-title="Add Products"
                                    @click="SearchItems()">
                                    <i class="fa fa-plus pr-1"></i>{{ $t('AddProducts') }}
                                </a>
                            </small>
                        </h5>
                    </div>
                </div>
            </Form>


            <SearchItems v-if="IsSearchItem" :TreeValue="TreeValue" @CloseSearchItem="CloseSearchItem"
                @addListData="addListData">
            </SearchItems>


        </div>
        <div class="row">
            <div class="col-12 mb-2 mt-2">
                <div class="mr-2 d-inline-block">
                    <span class="legend-Progress colorboxsquare"></span>{{ $t('ContainerProducts') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="text-danger font-weight-bold">*</span> {{ $t('MandatoryItems') }}
                </div>
                <span class="font-weight-bold" v-if="requsitionTime > 0" id="spnRemainingTime">{{ $t('RemainingTime')
                    }}</span>
                <div class="mr-2 d-inline-block" v-if="requsitionTime > 0">
                    <div id="timer" class="DivRequisition text-primary countdown_req counttimer" data-ison="0"
                        data-totaltime="5">
                        <div class="newdiv pullleft">&nbsp; {{ formattedTime }}</div>
                    </div>
                </div>
            </div>
        </div>
        <listOfProduct :listData="listData"></listOfProduct>

    </section>
</template>

<script>
import DataService from '../../service/DataService';
import SearchItems from '../../components/Modal/SearchItems.vue';
import listOfProduct from '../../components/Modal/ListOfProduct.vue'
import Treeselect from '@riophae/vue-treeselect';
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import { DateTime } from 'luxon';

export default {
    components: { SearchItems, listOfProduct, 'tree-view': Treeselect },

    data() {
        return {
            isLoadinges: false,
            DepartmentData: [],
            txtDepartment: '',
            UsersData: [],
            DateofAssignment: '',
            Users: '',
            Remarks: '',
            DateofAssignmentTill: '',
            Groupname: '',
            popover: {
                visibility: 'click'
            },
            UserId: null,
            GetParentDetails: null,
            SelfCheckout: null,
            RequestFrom: '',
            isUsedFor: '',
            selectedOption: '',
            checked: true,
            unchecked: false,
            txtAssignUserType: '',
            disabled: false,
            NoRecordfound: true,
            ClientData: [],
            LegendArray: [],
            txtLocation: '',
            LocationData: [],
            GroupData: '',
            IsSearchItem: false,
            options: [],
            TreeValue: null,
            listData: [],
            dataArray: [],
            assignToGroup: false,
            assignedFrom: '',
            startTime: '',
            assignedTill: '',
            endTime: '',
            maxRequisitionTime: 0,
            isTimeron: 0,
            requsitionTime: 0,
            Timezone: '',
            time: 0, // Initial time set to 2 minutes (in milliseconds)
            timerInterval: null,
            datepickerDisabled: false,
            treeViewDisabled: false

        }
    },
    computed: {
        formattedTime() {
            //debugger
            // Convert time to minutes and seconds
            const minutes = Math.floor(this.time / 60000);
            const seconds = ((this.time % 60000) / 1000).toFixed(0);
            return `${minutes}:${(seconds < 10 ? '0' : '')}${seconds}`;
        }
    },
    getMinDate(date) {
        // Assuming date is in "MM/DD/YYYY" format
        const parts = date.split('/');
        return `${parts[2]}-${parts[0]}-${parts[1]}`;
    },
    beforeDestroy() {
        clearInterval(this.timerInterval);
    },
    created: function () {

        //debugger;

        var vm = this;
        vm.Timezone = this.GetUserInfo.TimeZone;
        vm.GetLocationCheckOutByuserId()
        vm.LegendArray = [
            {
                class: 'legends_profile colorboxsquare',
                text: ('Pending Approval')
            }
        ]
        vm.UserId = vm.GetUserInfo.ID;
        vm.searchByProductFromCheckAvailablity();

        if (vm.$route.query.assignedFrom != 'undefined' && vm.$route.query.assignedFrom != '' && vm.$route.query.assignedFrom != null) {

            debugger;

            var currentDate = vm.$route.query.assignedFrom;
            var endDate = vm.$route.query.assignedTill;
            var startTime = vm.$route.query.startTime;
            var endTime = vm.$route.query.endTime;
            var startDateString = currentDate + ' ' + startTime;
            var endDateString = endDate + ' ' + endTime;
            vm.DateofAssignment = null;
            vm.DateofAssignmentTill = null;
            vm.DateofAssignment = this.formatAMPM(new Date(startDateString));
            vm.DateofAssignmentTill = this.formatAMPMM(new Date(endDateString));
            vm.datepickerDisabled = true
            vm.treeViewDisabled = true;

            setTimeout(() => {
                vm.TreeValue = vm.$route.query.locationId;
            }, 1000);

        } else {
            vm.DateofAssignment = this.formatAMPM(new Date());
            vm.DateofAssignmentTill = new Date();
            vm.DateofAssignmentTill.setDate(this.DateofAssignmentTill.getDate() + 1);
            vm.DateofAssignmentTill = this.formatAMPMM(this.DateofAssignmentTill);
        }
        if (vm.TreeValue == null || vm.TreeValue == '') {
            vm.GetLocationByCompanyId();
        }
    },
    methods: {
        formatAMPM: function (date) {
            //debugger
            var hours = date.getHours();
            var minutes = date.getMinutes();
            var Newdate = date.getDate();
            var month = date.getMonth();
            var year = date.getFullYear();
            var AmPm = hours >= 12 ? 'PM' : 'AM';
            hours = hours % 12;
            hours = hours ? hours : 12;
            minutes = minutes < 10 ? '0' + minutes : minutes;
            var strTime = (month + 1) + '/' + Newdate + '/' + year + ' ' + hours + ':' + minutes + ' ' + AmPm;
            return strTime;
        },
        formatAMPMM: function (date) {
            // debugger
            var hours = date.getHours();
            var minutes = date.getMinutes();
            var Newdate = date.getDate();
            var month = date.getMonth();
            var year = date.getFullYear();
            var AmPm = hours >= 12 ? 'PM' : 'AM';
            hours = hours % 12;
            hours = hours ? hours : 12;
            minutes = minutes < 10 ? '0' + minutes : minutes;
            var strTime = (month + 1) + '/' + Newdate + '/' + year + ' ' + hours + ':' + minutes + ' ' + AmPm;
            return strTime;
        },
        searchByProductFromCheckAvailablity: function () {
            var vm = this;
            vm.isLoadinges = true;

            vm.txtProductType = '';
            vm.txtProductName = '';
            vm.productUniqueId = '';
            var reqForm = vm.$route.query.assetrequisition;
            var userId = vm.$route.query.issueTo;
            var currentDate = vm.$route.query.assignedFrom;
            var endDate = vm.$route.query.assignedTill;
            vm.TreeValue = vm.$route.query.locationId;
            var startTime = vm.$route.query.startTime;
            var endTime = vm.$route.query.endTime;
            vm.productUniqueId = vm.$route.query.uniqueIds;

            var url = vm.$route.fullPath != '' ? vm.$route.fullPath.split('?')[1] : '';
            if (url != undefined) {
                DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                    //debugger
                    if (response.data.length > 0) {
                        response.data.forEach(ele => {
                            ele.IsChecked = false;
                            ele.ReqQty = '';
                            ele.Mandatory = false;
                            ele.IsDelete = true;

                        })
                        vm.listData = response.data;
                        vm.isLoadinges = false;
                        if (vm.$route.query.assignedFrom != 'undefined' && vm.$route.query.assignedFrom != '' && vm.$route.query.assignedFrom != null) {
                            vm.GetCheckOutDetails();
                        }
                        else {
                            vm.DateofAssignment = this.formatAMPM(new Date());
                            vm.DateofAssignmentTill = new Date();
                            vm.DateofAssignmentTill.setDate(this.DateofAssignmentTill.getDate() + 1);
                            vm.DateofAssignmentTill = this.formatAMPMM(this.DateofAssignmentTill);
                        }
                        setTimeout(function () {
                            vm.CheckBoxBootstrap();
                        }, 100);
                    }
                });

            }
            else {
                vm.isLoadinges = false;
            }
        },
        GetLocationByCompanyId: async function () {
            var vm = this;
            vm.isLoading = true;
            var params = `reqForm=ASSETREQUISITION`
            await DataService.GetLocationByCompanyId(params)
                .then(function (response) {
                    try {
                        let dataArray = JSON.parse(response.data).data;
                        //if (Array.isArray(dataArray) && dataArray.length > 1) {
                        if (dataArray.length > 1) {
                            vm.TreeValue = null;
                        } else {
                            vm.TreeValue = dataArray[0].COMPANY_LOCATION_ID;
                        }
                    } catch (error) {
                        console.error("Error parsing JSON:", error);
                    }
                    setTimeout(function () {
                        this.$refs.addCheckOut.reset();
                    }, 500);

                });
        },
        GetLocationCheckOutByuserId: async function () {
            var vm = this;
            let locationId = null;
            var url = `reqForm=transfer`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });

                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

        SearchItems: function () {

            if (this.TreeValue == null) {
                this.ShowAlert(
                    this.$t("Please Select location First"),
                    "warning",
                    this.$t("Alert")
                );
                return;
            } else {
                this.IsSearchItem = true;
            }
        },
        CloseSearchItem: function () {
            this.IsSearchItem = false;
        },

        onCancel: function () {
            this.$router.push({ name: 'ProductTypeCheckOutRequest' });
        },

        addListData: function (addListData) {

            var vm = this;
            if (addListData != undefined) {
                vm.listData.push(addListData);
            }

        },


        async GetCheckOutDetails() {
            var vm = this;

            await DataService.GetCheckOut().then((response) => {
                if (response.data.maxRequisition_Time != 0) {
                    vm.maxRequisitionTime = response.data.maxRequisitionTime;
                    vm.requsitionTime = parseInt(vm.maxRequisitionTime) / 60;

                    if (vm.timerInterval) {
                        clearInterval(vm.timerInterval);
                    }

                    this.time = parseInt(vm.requsitionTime) * 60000;

                    this.timerInterval = setInterval(() => {
                        this.time -= 1000;

                        if (this.time <= 0) {
                            clearInterval(this.timerInterval);

                            if (this.$route.query.reqFrom == 'CheckBarcodeAssetAvailability') {
                                return this.$router.push({ name: "ProductListing" });
                            }
                            else if (this.$route.query.reqFrom == 'assetrequisition') {
                                return this.$router.push({ name: "ProductListing" });
                            }
                            const urlParams = new URLSearchParams(window.location.search);
                            const reqfrom = urlParams.get('From') ? urlParams.get('From') : urlParams.get('From');
                            if (reqfrom == 'ManageProdcut') {
                                vm.Redirect();
                            }
                            else if (reqfrom == 'MyProdcut') {
                                vm.Redirects();
                            }
                            else {
                                location.reload(true);
                            }
                        }
                    }, 1000);

                    return true;
                }

                if (response.data.maxRequisition_Time == 0) {
                    vm.ShowAlert(vm.$t('MaxRequisitionCountMessage'), "failure", true, vm.$t('Alert'));
                    return false;
                }
            });
        },

        // async GetCheckOutDetails() {

        //     var vm = this;

        //     await DataService.GetCheckOut().then((response) => {
        //         if (response.data.maxRequisition_Time != 0) {
        //             vm.maxRequisitionTime = response.data.maxRequisitionTime;
        //             vm.requsitionTime = parseInt(vm.maxRequisitionTime) / 60;
        //             vm.isTimeron = 0;
        //             if (parseInt(vm.maxRequisitionTime) <= 0) {
        //                 vm.isTimeron = 1;
        //             }
        //             this.time = parseInt(vm.requsitionTime) * 60000;

        //             this.timerInterval = setInterval(() => {
        //                 // Decrement time by 1 second
        //                 this.time -= 1000;

        //                 // If time reaches 0, clear the interval
        //                 if (this.time === 0) {
        //                     // clearInterval(this.timerInterval);                           
        //                     if (this.$route.query.reqFrom == 'CheckBarcodeAssetAvailability') {
        //                         return this.$router.push({ name: "ProductListing" });

        //                     }
        //                     else if (this.$route.query.reqFrom == 'assetrequisition') {
        //                         return this.$router.push({ name: "ProductListing" });
        //                     }
        //                     const urlParams = new URLSearchParams(window.location.search);
        //                     const reqfrom = urlParams.get('From') ? urlParams.get('From') : urlParams.get('From');
        //                     if (reqfrom == 'ManageProdcut') {
        //                         vm.Redirect();
        //                     }
        //                     else if (reqfrom == 'MyProdcut') {
        //                         vm.Redirects();
        //                     }

        //                     else {
        //                         location.reload(true);
        //                     }

        //                 }
        //                 // if (vm.timerInterval) {
        //                 //     clearInterval(vm.timerInterval);
        //                 //     vm.timerInterval = null;
        //                 // }
        //             }, 1000);
        //             return true;
        //         }
        //         if (response.data.maxRequisition_Time == 0) {
        //             vm.ShowAlert(vm.$t('MaxRequisitionCountMessage'), "failure", true, vm.$t('Alert'));
        //             return false;
        //         }
        //     });
        // },
        Redirect: function () {
            this.$router.push({ name: "ProductListing" });
        },
        Redirects: function () {
            this.$router.push({ name: "MyProducts" });
        },
        saveCheckout: function () {
            var vm = this;
            vm.isLoadinges = true;
            if (vm.listData.length == 0) {
                vm.isLoadinges = false;
                return vm.ShowAlert(vm.$t("Please add at least one item"), "warning", vm.$t('Alert'));
            }
            vm.$refs.addCheckOut.validate().then(result => {
                if (result) {
                    var vm = this;
                    var itemnames = "";
                    var itemList = [];
                    vm.listData.forEach(ele => {
                        if (ele.AssetCatalogueId != undefined) {
                            let obj = {
                                AssetCatalogUniqueId: ele.ParentUniqueId > 0 ? ele.ParentUniqueId : 0,
                                AssetCatalogId: ele.AssetCatalogueId,
                                AssetQuantity: ele.PendingTotalAvailableItems,
                                LocationId: ele.LocationId
                            };

                            var itemId = `${ele.AssetCatalogName}(${ele.ItemCode})`;
                            if (itemList.indexOf(itemId) == -1) {
                                itemList.push(itemId);
                            }
                            if (!vm.dataArray.some(x => x.AssetCatalogUniqueId == obj.AssetCatalogUniqueId && x.AssetCatalogId == obj.AssetCatalogId)) {
                                vm.dataArray.push(obj);
                            }
                            itemnames = itemList.join(',');
                            vm.isLoadinges = false;
                        }
                    });
                    var Data = {
                        UserId: vm.UserId,
                        AssignedFrom: vm.formatTimeToCustomFormat(vm.DateofAssignment),
                        AssignedTill: vm.formatTimeToCustomFormat(vm.DateofAssignmentTill),
                        Comment: vm.Remarks,
                        GroupRequistionAssestInfo: vm.dataArray,
                        itemNames: itemnames
                    }
                    var mainObj = JSON.stringify(Data);
                    DataService.SaveGroupRequisition(mainObj).then(response => {
                        if (response.data.msg == 'success') {
                            vm.isLoadinges = false;
                            vm.ShowAlert(vm.$t("Check out request Submitted successfully"), "success", vm.$t('Alert'));
                            vm.$router.push({ path: "/AssetRequisition/MyRequisitions" });
                        }
                        else {
                            vm.isLoadinges = false;
                            vm.ShowAlert(vm.$t("Something went wrong "), "warning", vm.$t('Alert'));
                        }
                    })
                }
                else {
                    vm.isLoadinges = false;
                    vm.ShowAlert(vm.$t("Please Correct the Highlighted errors"), "warning", vm.$t('Alert'));
                }
            })
        },
        BindItemTypeByUserId: function (ths) {
            let url = `type=ITEMCODE&issueTo=&locationId=${ths}&requestType=ASSIGN&requestFrom=TRANSFER`;
            DataService.AssignGetAssetType(url).then(response => {
                this.AssetTypeData = response.data;
            });
        },

        formatTimeToCustomFormat(inputTime) {
            if (inputTime != null && inputTime !== '') {
                const offsetParts = this.Timezone.split(':');
                const hoursOffset = parseInt(offsetParts[0], 10);
                const minutesOffset = parseInt(offsetParts[1], 10);
                const totalOffsetMinutes = hoursOffset * 60 + minutesOffset;

                const targetTimeZone = `Etc/GMT${totalOffsetMinutes >= 0 ? '+' : ''}${totalOffsetMinutes}`;
                const outputLuxonDateTime = inputLuxonDateTime.setZone(targetTimeZone);
                const formattedDateTime = outputLuxonDateTime.toFormat("MM/dd/yyyy h:mm");
                return formattedDateTime;
            } else {
                return "";
            }
        }

    }
}
</script>

<style>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.form-control .vue-treeselect__control {
    border: none !important;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}

.vue-treeselect__single-value {
    color: #333;
    line-height: 22px;
}

.vue-treeselect .vue-treeselect__placeholder.vue-treeselect-helper-zoom-effect-off {
    padding: 1px;
}

.form-control .vue-treeselect__control .vue-treeselect__single-value {
    padding-left: 0;
}

.vue-treeselect--single .vue-treeselect__input {
    width: 100%;
    height: 100%;
    border: none;
    padding: 0;
}
</style>